.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 50px);
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.info {
  width: 20%;
  margin-top: 10px;
  display: flex;
}

.info-item {
  margin: 3px;
  font-size: 13px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

.search {
  background-color: #007bff;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

